<template>
  <div>
    <van-nav-bar title="用户登录" />
    <van-form @submit="onSubmit">
      <van-field
        readonly
        clickable
        name="fcom_id"
        :value="value"
        label="选择地区"
        placeholder="点击选择地区"
        @click="showPicker = true"
        :rules="[{ required: true, message: '请选择地区' }]"
      />
      <van-popup v-model="showPicker" position="bottom">
        <van-picker
          title="选择地区"
          show-toolbar
          :columns="chose_place"
          @confirm="onConfirm"
          value-key="fcom_id"
          @cancel="showPicker = false"
        />
      </van-popup>
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit">立即登录</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { apigetplace } from "@/api/index";
import { apiplacesave } from "@/api/index";
import { apilogin } from "@/api/index";
import { Dialog } from 'vant';
import storage from "@/assets/js/storage";
export default {
  name: "Homeindex",
  data() {
    return {
      chose_place: [],
      showPicker: false,
      value: ""
    };
  },
  methods: {
    getlist() {
      var _this=this
      apigetplace().then(res => {
        _this.chose_place = res.data.data;
      });
    },
    onConfirm(value) {
      this.value = value.fcom_id;
      this.showPicker = false;
    },
    onSubmit(values) {
      apiplacesave(values).then(res => {
        if (res.data.code == 200) {
          this.$router.push({
            path: "/home"
          });
        }
      });
    }
  },
  created() {   
        apilogin(1).then(res => {
              if (res.data.code == 200) {
                storage.set("openID", res.data.ding_user_id);
                storage.set("show", res.data.role_health);
                storage.set("repair", res.data.role_repair);
                storage.set("place",res.data.user_area);
                if (res.data.user_area_flag== true) {         
                  self.$router.push({ path: '/home' })
                }
              }
            });
        let self = this
        dd.runtime.permission.requestAuthCode({
          corpId: "ding4b76df8fb11dede535c2f4657eb6378f",
          onSuccess: function(result) {
            //通过corpId，code获取userid
            apilogin(result.code).then(res => {
              if (res.data.code == 200) {
                storage.set("openID", res.data.ding_user_id);
                storage.set("show", res.data.role_health);
                storage.set("repair", res.data.role_repair);
                storage.set("place",res.data.user_area);
                if (res.data.user_area_flag== true) {         
                  self.$router.push({ path: '/home' })
                }
              }
            });
          },
          onFail: function(err) {
            alert(JSON.stringify(err));
          }
        });
      this.getlist();
  }
};
</script>

<style>
</style>


